.icon-picker-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.icon-picker-item {
  background-color: white;
  height: 50px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  border-radius: 5px;
  border: transparent 1px solid;
  margin-right: 10px;
  margin-top: 10px;
  cursor: pointer;
}

.icon-picker-item:hover {
  -webkit-box-shadow: 10px 10px 41px 0px rgba(0,0,0,0.17);
  -moz-box-shadow: 10px 10px 41px 0px rgba(0,0,0,0.17);
  box-shadow: 10px 10px 41px 0px rgba(0,0,0,0.17);
}

.icon-picker-item-active {
  border: var(--primary-color) 1px solid;
}