.simulation-container {
  background-color: white;
  display: flex;
  border-radius: 5px;
  margin-top: 6px;
  margin-bottom: 6px;
}

.status-indicator {
  width: 10px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.status-indicator-pending {
  background-color: var(--pending-color);
}

.status-indicator-running {
  background-color: lightgray;
}

.status-indicator-error {
  background-color: var(--danger-color);
}

.status-indicator-passed {
  background-color: var(--success-color);
}

.simulation-inside-container {
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
  flex: 1;
  align-items: center;
  font-size: 13px;
}

.status-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.status-container p {
  margin-right: 10px;
}

.status-column-passed {
  color: var(--success-color);
}

.status-column-error {
  color: var(--danger-color);
}

.details-column {
  width: 60px;
  text-align: center;
}

.details-column {
  color: black;
}

.details-column-link:hover {
  opacity: 0.5;
}
