.template-modal-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.template-modal-content {
  display: flex;
  min-width: 600px;
  max-height: 450px;
  background-color: white;
  border-radius: 5px;
  padding: 0;
  flex-direction: column;
}

.template-modal-content-header {
  background-color: var(--background-color);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  padding: 15px;
}

.template-modal-content-header h2 {
  margin: 0;
  padding: 0;
  color: black;
}

.template-modal-content-inside {
  min-height: 100px;
  padding: 20px;
  display: flex;
  flex-direction: row;
}

.template-modal-content-column {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: scroll;
  overflow-x: unset;
  overflow-y: auto;
}

.template-modal-add-empty-button {
  background-color: var(--background-color);
  flex: 1;
  border-radius: 5px;
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.template-modal-add-empty-button:hover {
  opacity: 0.5;
}

.template-modal-add-empty-button-icon {
  color: var(--primary-color);
  font-size: 70px;
}

.template-modal-separator {
  width: 20px;
}

.template-modal-template-item {
  background-color: var(--background-color);
  border-radius: 5px;
  cursor: pointer;
  margin: 4px 0 4px 0;
  padding: 10px;
}

.template-modal-template-item h5 {
  margin: 0;
  font-weight: 600;
}

.template-modal-template-item p {
  text-align: left;
  font-size: 10px;
  margin: 0;
  margin-top: 10px;
}
