.simulation-bottom-bar {
  height: 60px;
  width: 100%;
  background-color: white;
  border-top-left-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 25px;
  padding: 0 20px 0 20px;
  -webkit-box-shadow: 10px 10px 97px 0px rgba(0, 0, 0, 0.12);
  -moz-box-shadow: 10px 10px 97px 0px rgba(0, 0, 0, 0.12);
  box-shadow: 10px 10px 97px 0px rgba(0, 0, 0, 0.12);
}

.simulation-bottom-bar-outside {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
}

.simulation-bottom-bar-done-step {
  color: var(--success-color);
  font-size: 15px;
  position: absolute;
  margin-left: 25px;
  margin-top: -15px;
}

.simulation-bottom-bar-icon-container {
  display: flex;
  align-items: center;
}

.simulation-bar-button-container {
  width: 150px;
}

.simulation-padding-container {
  display: flex;
  flex-grow: 1;
  flex: 1;
  min-height: 0;
}

.simulation-flex-container {
  padding-left: 10%;
  padding-right: 2%;
  overflow-y: auto;
  min-height: 0;
  height: -moz-calc(100% - 60px);
  height: -webkit-calc(100% - 60px);
  height: -o-calc(100% - 60px);
  height: calc(100% - 60px);
  flex: 1;
  opacity: 1;
  transition: flex 100ms linear, opacity 600ms linear, width 50ms linear;
}

.simulation-flex-container-removed {
  flex: 0.00001;
  opacity: 0;
  width: 0px;
  transition: flex 400ms linear, opacity 100ms linear, width 50ms linear;
}

.simulation-flex-container:last-of-type {
  padding-left: 2%;
  padding-right: 10%;
}

.device-type-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.device-type-list-element {
  width: 105px;
  background-color: white;
  min-height: 70px;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 20px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: transparent 1px solid;
  font-size: 40px;
  color: var(--primary-color);
  padding: 20px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
}

.text-container {
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 13px;
  margin-top: 10px;
  color: black;
  text-align: center;
}

.device-type-list-element:hover {
  -webkit-box-shadow: 10px 10px 41px 0px rgba(0, 0, 0, 0.17);
  -moz-box-shadow: 10px 10px 41px 0px rgba(0, 0, 0, 0.17);
  box-shadow: 10px 10px 41px 0px rgba(0, 0, 0, 0.17);
}

.device-type-list-element p {
  color: black;
  margin-top: 10px;
  margin-bottom: 0;
  font-size: 12px;
}

.device-type-list-element-active {
  border: var(--primary-color) 1px solid;
}

.section {
  display: flex;
  flex-direction: column;
}

.medium-list {
  display: flex;
  flex-direction: column;
}

.medium-list-element {
  flex: 1;
  background-color: white;
  height: 20px;
  margin-top: 10px;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  align-items: center;
  border: transparent 1px solid;
  font-size: 30px;
  color: var(--primary-color);
  padding: 20px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
}

.medium-text-container {
  flex: 1;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 15px;
  color: black;
  margin-left: 20px;
}

.medium-list-element:hover {
  -webkit-box-shadow: 10px 10px 41px 0px rgba(0, 0, 0, 0.17);
  -moz-box-shadow: 10px 10px 41px 0px rgba(0, 0, 0, 0.17);
  box-shadow: 10px 10px 41px 0px rgba(0, 0, 0, 0.17);
}

.medium-list-element p {
  color: black;
  margin-top: 10px;
  margin-bottom: 0;
}

.medium-list-element-active {
  border: var(--primary-color) 1px solid;
}

.medium-constants-row {
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
}

.medium-constants-row:first-of-type {
  margin-top: 15px;
}

.medium-constants-column {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.medium-constants-column:last-of-type {
  margin-left: 30px;
}

.separator {
  height: 1px;
  background-color: lightgray;
  margin: 20px;
}

.simulation-flex-container h3 {
  margin-bottom: 0;
  margin-top: 35px;
}

.simulation-flex-container h3:first-of-type {
  /* margin-bottom: 15px; */
  margin-top: 0;
}

/* animations */
.simulation-animation-enter {
  opacity: 0;
  transform: translateY(1000px);
}
.simulation-animation-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 600ms, transform 600ms;
}

.simulation-animation-exit {
  opacity: 1;
}

.simulation-animation-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 600ms, transform 600ms;
}

footer {
  display: none;
}

.simulation-flex-container .checkbox-container {
  margin-top: 15px;
}

.no-content-container {
  position: absolute;
  top: 45%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.no-content-container p:first-of-type {
  font-weight: 600;
  color: var(--primary-color);
}

.no-content-container p:last-of-type {
  font-size: 10px;
  margin-top: 0;
}

.channel-name-container {
  display: flex;
}

.channel-name-container input {
  /* flex: 1; */
}

.channel-name-container .round-button-remove {
  margin-right: 0;
  margin-left: 15px;
}

.start-button {
  height: 150px;
  width: 150px;
  background-color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 60px;
  border-radius: 50%;

  box-shadow: 0 0 0 0 rgba(255, 127, 80, 1);
  transform: scale(1.1);
  animation: pulse 4s infinite;
  margin-bottom: 20px;
  margin-top: -60px;
}

.start-button:hover {
  transform: scale(1.1);
  animation: pulse 2s infinite;
  opacity: 0.9;
}

@keyframes pulse {
  0% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(255, 127, 80, 0.7);
  }

  70% {
    transform: scale(1.1);
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0);
  }

  100% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
}

@keyframes scale-down {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(0);
  }
}

.simulation-error-container {
  font-size: 50px;
  color: var(--primary-color);
}

.simulation-error-container p {
  padding: 0;
}

.simulation-error-container p:last-of-type {
  color: black;
  cursor: pointer;
  text-decoration-line: underline;
}

.delete-input-conatiner {
  flex: 1;
  display: flex;
  flex-direction: column;
}
