.back-text {
  text-align: left;
  color: var(--primary-color);
  cursor: pointer;
}

.back-text:hover {
  opacity: 0.6;
}

.details-error-container {
  background-color: white;
  white-space: pre-wrap;
  font-size: 14px;
  padding: 15px;
  border-radius: 5px;
  word-break: break-all;
  margin-top: 10px;
}

.details-container {
  text-align: left;
}

.details-container p {
  text-align: left;
  margin-block-start: 0em;
}

.simulation-details h5 {
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-bottom: 5px;
}

.status-text-error {
  color: var(--danger-color);
}

.status-text-passed {
  color: var(--success-color);
}

.passed-result {
  display: flex;
  background-color: white;
  font-size: 12px;
}

.passed-header {
  display: flex;
  background-color: rgba(250, 250, 250, 0.986);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  padding: 10px;
  /* color: ; */
  justify-content: center;
  margin-top: 15px;
}

.passed-result-column-bold {
  font-weight: 700;
}

.passed-result-column {
  flex: 1;
  display: flex;
  justify-content: center;
  padding: 10px;
}

.results-messages {
  text-align: left;
  white-space: pre-line;
  line-height: 8px;
  font-size: 10px;
  margin-top: 15px;
  margin-bottom: 15px;
}
