.diagram-container{
	/* background: #333333; */
	width: 100%;
  height: 100%;
}

.custom-node{
	background-color: white;
	border-radius: 5px;
	display: flex;
  flex-direction: column;
  border-radius: 5px;
  position: relative;
  -webkit-box-shadow: 10px 10px 41px 0px rgba(0,0,0,0.17);
  -moz-box-shadow: 10px 10px 41px 0px rgba(0,0,0,0.17);
  box-shadow: 10px 10px 41px 0px rgba(0,0,0,0.17);
}

.node-header {
  background-color: var(--primary-color);
  flex: 1;
  padding: 10px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 25px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}



.node-header-inside {
  display: flex;
  align-items: center;
}

.node-header-text {
  display: flex;
  flex-direction: column;
  
}

.node-header label {
  margin-left: 10px;
  margin-top: 0;
  margin-bottom: 0;
}

.node-header label:last-of-type {
  font-size: 8px;
  opacity: 0.8;
}

.node-remove-button {
  height: 20px;
  width: 20px;
  background-color: white;
  border-radius: 30px;
  color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-left: 20px;
}

.node-remove-button:hover {
  opacity: 0.5;
}

.node-ports-container {
  display: flex;
  padding-top: 10px;
  padding-bottom: 10px;
}

.ports {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.out-ports {
  align-items: flex-end;
}

.ports-names {
  display: flex;
  flex-direction: column;
  min-width: 100px;
  align-items: center;
}

.ports-names label {
  margin:0;
  margin-top: 5px;
  margin-bottom: 5px;
  height: 20px;
  vertical-align: center;
}

.node-port {
  width: 40px;
  height: 20px;
  margin-top: 5px;
  margin-bottom: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.node-port:hover {
  opacity: 0.5;
}

.node-port label {
  margin: 0;
  font-size: 10px;
}

.node-port-in {
  background-color: lightgray;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.node-port-out {
  background-color: var(--primary-color);
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  color: white;
}

.diagram-body {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.diagram-content {
  display: flex;
  flex-grow: 1;
}

.diagram-layer {
  position: relative;
  flex-grow: 1;
}

.diagram-tray {
  min-width: 250px;
  flex-grow: 0;
  flex-shrink: 0;
  -webkit-box-shadow: 10px 10px 41px 0px rgba(0,0,0,0.17);
  -moz-box-shadow: 10px 10px 41px 0px rgba(0,0,0,0.17);
  box-shadow: 10px 10px 41px 0px rgba(0,0,0,0.17);
  background-color: white;
  padding: 20px;
  max-height: 90vh;
  overflow-y: auto;
}

.tray-header-description {
  padding-bottom: 15px!important;
}

.diagram-tray h3 {
  margin: 0;
}

.diagram-tray-item {
  color: var(--primary-color);
  display: flex;
  align-items: center;
  padding: 10px;
  margin: 10px 0px;
  border-radius: 5px;
  margin-bottom: 2px;
  cursor: pointer;
  background-color: var(--background-color);
  font-size: 25px;
}

.diagram-tray-item label {
  margin: 0;
  margin-left: 8px;
  color: black;
}

.srd-diagram .srd-default-link path{
  
  stroke: #000;
 
}

